<template>
  <v-hover v-slot="{ hover }">
  <v-card
    :elevation="hover ? 3 : 3"
    :class="{ 'on-hover': hover }"
  >
    <v-card-title style="padding-bottom: 0%; padding-top: 5px;">
      <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-icon
          v-bind="attrs"
          v-on="on"
        >
          menu
        </v-icon>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in ['OPEN连接', 'CLOSE连接', '调试指令', '调试代码']"
          :key="index"
          @click="ConnAction(item)"
        >
          <v-list-item-title>{{ item }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
      <span class="font-weight-light">{{ conn.name }}</span>
    </v-card-title>
    <v-divider></v-divider>

    <div :id="'terminal-' + containerName + ':' + conn.name"
    :style="'max-height:' + xtermPageHight + 'px;min-height:' + xtermPageHight + 'px;'"></div>

    <v-card-actions style="padding-bottom: 0%;  padding-top: 0%;">
      <v-btn
        color="primary" 
        :href="''"
        target="_blank"
      >LOG</v-btn>
    </v-card-actions>
  </v-card>
  </v-hover>
</template>

<script>
import 'xterm/css/xterm.css'
import { Terminal } from 'xterm';
import { FitAddon } from 'xterm-addon-fit';
import { KunlunApi2 } from '../api'

export default {
  props: ['conn', 'containerName', 'message', 'xtermPageHight', 'connListLength'],
  data () {
    return {
      term: null,
      fitAddon: null,
      userInput: '',
    };
  },
  watch: {
    'message': {
      handler: function (message1) {
        if (message1.name === this.conn.name) {
          this.term.write(message1.data);
        }
        this.term.scrollToBottom();
      },
      deep: true,
    },
    'connListLength': {
      handler: function () {
        this.fitAddon.fit()
      }
    }
  },
  mounted () {
    const connTerminal = document.getElementById('terminal-' + this.containerName + ':' + this.conn.name)
    this.term = new Terminal({
      cursorBlink: 5,
      scrollback: 1000,
      cursorStyle: 'bar',
      disableStdin: false
    })
    var _this = this
    this.fitAddon = new FitAddon()
    this.term.loadAddon(this.fitAddon)
    this.term.open(connTerminal)
    this.fitAddon.fit()
    setTimeout(() => {
      this.getCurrentLog()
    }, 100)
    this.term.onData(function (key) {
      _this.userInput = key
      _this.submitUserInput()
    })
  },
  destroyed () {
    this.term.dispose()
  },
  methods: {
    submitUserInput () {
      var data = { type: 'input', cmd: this.userInput, container: this.containerName, conn: this.conn.name }
      this.$emit('submitUserInput', data)
    },
    getCurrentLog () {
      KunlunApi2({ service: 'get-current-log', conn: this.containerName + ':' + this.conn.name })
        .then(response => {
          let logs = response.data.data
          logs.split('\n').forEach(element => {
            this.term.write(element + '\r\n')
          })
          // console.log(logs)
        })
        .catch(e => {
          console.log(e)
        })
    },
    ConnAction (action) {
      if (action === 'OPEN连接' ) {
        let data = { type: 'input', cmd: '::OPEN', container: this.containerName, conn: this.conn.name }
        this.$emit('submitUserInput', data)
      }
      else if (action === 'CLOSE连接') {
        let data = { type: 'input', cmd: '::CLOSE', container: this.containerName, conn: this.conn.name }
        this.$emit('submitUserInput', data)
      } else if (action === '调试指令') {
        this.$emit('connectionListClick', { name: 'CMD-' + this.conn.name, created_at: 0 })
      } else if (action === '调试代码') {
        this.$emit('connectionListClick', { name: 'CODE-' + this.conn.name, created_at: 0 })
      }
    }
  }
};
</script>

