<template>
  <v-hover v-slot="{ hover }">
  <v-card
    :elevation="hover ? 3 : 3"
    :class="{ 'on-hover': hover }"
  >
    <v-card-title style="padding-bottom: 0%; padding-top: 5px;">
      <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-icon
          v-bind="attrs"
          v-on="on"
        >
          menu
        </v-icon>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in ['关闭窗口']"
          :key="index"
          @click="ConnAction()"
        >
          <v-list-item-title>{{ item }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
      <span class="font-weight-light">{{ conn.name }}</span>
    </v-card-title>
    <v-divider></v-divider>

    <div>
      <codemirror  :style="'max-height:' + xtermPageHight + 'px;min-height:' + xtermPageHight + 'px;'"
      ref="cmEditor" v-model="code" :options="cmOptions" />   
      <v-btn style="margin-left: 5px;"
        color="primary" 
        @click="submitUserInput"
      >run<v-icon>play_arrow</v-icon></v-btn>
      <v-btn style="margin-left: 5px;"
        color="orange" 
        @click="setCmdList"
      >Save<v-icon>save</v-icon></v-btn>
    </div>
  </v-card>
  </v-hover>
</template>

<script>
import 'codemirror/mode/python/python.js'
import 'codemirror/theme/darcula.css'
import { KunlunApi2 } from '../api'

export default {
  props: ['conn', 'containerName', 'xtermPageHight', 'connListLength'],
  data () {
    return {
      code: '',
      cmOptions: {
        tabSize: 4,
        indentWithTabs: true,
        lineWrapping: true,
        lineNumbers: true,
        spellcheck: true,
        indentUnit: 4,
        mode: 'text/x-cython',
        theme: 'darcula'
      },
      userInput: '',
    };
  },
  mounted () {
    this.getCmdList()
    this.$refs.cmEditor.codemirror.setSize(null, this.xtermPageHight - 2)
  },
  methods: {
    clearCode () {
      this.code = ''
    },
    submitUserInput () {
      this.userInput = this.$refs.cmEditor.codemirror.getSelection()
      if (!this.userInput.trim()) {
        let line = this.$refs.cmEditor.codemirror.getCursor().line
        this.userInput = this.$refs.cmEditor.codemirror.getLine(line).trim()
      }
      // console.log(this.userInput)
      var data = { type: 'cmd', cmd: this.userInput, container: this.containerName, conn: this.conn.name.split('-')[1] }
      this.$emit('submitUserInput', data)
    },
    setCmdList () {
      KunlunApi2({ service: 'set-cmd-list', name: this.containerName.split(':')[0], configs: this.code })
        .then(response => {
          console.log(response.data.data)
        })
        .catch(e => {
          console.log(e)
        })
    },
    getCmdList () {
      KunlunApi2({ service: 'get-cmd-list', name: this.containerName.split(':')[0] })
        .then(response => {
          this.code = response.data.data
        })
        .catch(e => {
          console.log(e)
        })
    },
    ConnAction () {
      this.$emit('connectionListClick', { name: this.conn.name, created_at: 0 })
    }
  }
};
</script>

<style scoped>

</style>