<template>
  <v-hover v-slot="{ hover }">
  <v-card
    :elevation="hover ? 3 : 3"
    :class="{ 'on-hover': hover }"
  >
    <v-card-title style="padding-bottom: 0%; padding-top: 5px;">
      <v-icon>menu</v-icon><span class="font-weight-light">EVENT</span>
    </v-card-title>
    <v-divider></v-divider>

    <div :id="'terminal-' + containerName + ':EVENT'"
    :style="'max-height:' + xtermPageHight + 'px;min-height:' + xtermPageHight + 'px;'"></div>

    <v-card-actions style="padding-bottom: 0%;  padding-top: 0%;">
      <v-btn
        color="primary" 
        :href="''"
        target="_blank"
      >LOG</v-btn>
    </v-card-actions>
  </v-card>
  </v-hover>
</template>

<script>
import 'xterm/css/xterm.css'
import { Terminal } from 'xterm';
import { FitAddon } from 'xterm-addon-fit';
import { KunlunApi2 } from '../api'


export default {
  props: ['containerName', 'message', 'xtermPageHight', 'connListLength'],
  data () {
    return {
      term: null,
      fitAddon: null
    };
  },
  watch: {
    'message': {
      handler: function (message1) {
        if (message1.name === 'EVENT') {
          this.writeLogs(message1.data)
        }
        this.term.scrollToBottom();
      },
      deep: true,
    },
    'connListLength': {
      handler: function () {
        this.fitAddon.fit()
      }
    }
  },
  mounted () {
    const connElement = document.getElementById('terminal-' + this.containerName + ':EVENT')
    this.term = new Terminal({
      cursorBlink: 0,
      scrollback: 10000,
      cursorStyle: 'bar',
      disableStdin: true,
    })
    this.fitAddon = new FitAddon()
    this.term.loadAddon(this.fitAddon)
    this.term.open(connElement)
    this.fitAddon.fit()
    this.getCurrentLog()
  },
  destroyed () {
    this.term.dispose()
  },
  methods: {
    getCurrentLog () {
      KunlunApi2({ service: 'get-current-log', conn: this.containerName + ':EVENT' })
        .then(response => {
          let logs = response.data.data
          // console.log(logs)
          this.writeLogs(logs)
        })
        .catch(e => {
          console.log(e);
        });
    },
    writeLogs (logs) {
      logs.split('\n').forEach(element => {
        if (element) {
          if (element.startsWith('ERROR')) {
            this.term.write('\u001b[01;31m\u001b[K' + element + '\r\n\u001b[m\u001b[K');
          }
          // else if (element.startsWith('INFO')) {
          //   this.term.write('\u001b[00;33m\u001b[K' + element + '\r\n\u001b[m\u001b[K');
          // }
          else {
            this.term.write(element + '\r\n');
          }
        }
      });
    }
  }
};
</script>

