<template>
  <v-hover v-slot="{ hover }">
  <v-card
    :elevation="hover ? 3 : 3"
    :class="{ 'on-hover': hover }"
  >
    <v-card-title style="padding-bottom: 0%; padding-top: 5px;">
      <v-icon>menu</v-icon><span class="font-weight-light">INFO</span>
    </v-card-title>
    <v-divider></v-divider>

    <div :id="'terminal-' + containerName + ':INFO'"
    :style="'max-height:' + xtermPageHight + 'px;min-height:' + xtermPageHight + 'px;'"></div>

    <v-card-actions style="padding-bottom: 0%;  padding-top: 0%; height: 36px;">
    </v-card-actions>
  </v-card>
  </v-hover>
</template>

<script>
import 'xterm/css/xterm.css'
import { Terminal } from 'xterm';
import { FitAddon } from 'xterm-addon-fit';

export default {
  props: ['containerInfo', 'containerName', 'xtermPageHight', 'connListLength'],
  data () {
    return {
      term: null,
      fitAddon: null
    };
  },
  watch: {
    'connListLength': {
      handler: function () {
        this.fitAddon.fit()
      }
    }
  },
  mounted () {
    const xtermElement = document.getElementById('terminal-' + this.containerName + ':INFO')
    this.term = new Terminal({
      cursorBlink: 0,
      scrollback: 10000,
      cursorStyle: 'bar',
      disableStdin: true,
    })
    this.fitAddon = new FitAddon()
    this.term.loadAddon(this.fitAddon)
    this.term.open(xtermElement)
    this.fitAddon.fit()
    this.term.write(this.containerInfo);
  },
  destroyed () {
    this.term.dispose()
  },
};
</script>

