<template>
  <v-container fluid pa-0>
    <v-row no-gutters>
      <v-col cols="12">
      <ToolBar
        :version="version"
        @setMode="setMode"
      >
      </ToolBar>
      <v-divider></v-divider>
      <v-divider></v-divider>
      <v-divider></v-divider>
      <v-divider></v-divider>
      </v-col>
      <v-col cols="12" style="marign-top: 20px;">
        <v-card>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                v-bind="attrs"
                v-on="on"
                @click="getBackClick"
              ><v-icon>arrow_back</v-icon></v-btn>
            </template>
            <span>Back</span>
          </v-tooltip>
          <!-- // -->
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn style="margin-left: 3px;"
                :color="containerColor"
                v-bind="attrs"
                v-on="on"
                @click="containerActionClick"
              >{{ containerName }}
              <v-icon>{{ containerIcon }}</v-icon>
              <v-icon>{{ modeIcon }}</v-icon>
              </v-btn>
            </template>
            <span>{{ containerAction }}</span>
          </v-tooltip>
          <v-btn v-for="(conn, key1) in connectionList" :key="key1" style="margin-left: 3px;"
            color="primary"
            text
            @click="connectionListClick(conn)"
          >{{ conn.name }}</v-btn>
        </v-card>
      </v-col>
      <v-col :cols="12 / connListLength" v-for="(conn, key1) in connList" :key="key1" 
        style="margin-top: 4px; padding-right: 2px; padding-left: 2px;"
      >
        <InfoXterm v-if="conn.name === 'INFO'"
          :containerInfo="containerInfo"
          :containerName="containerName"
          :connListLength="connListLength"
          :xtermPageHight="xtermPageHight"
        ></InfoXterm>
        <EventXterm v-else-if="conn.name === 'EVENT'"
          :message="message"
          :containerName="containerName"
          :connListLength="connListLength"
          :xtermPageHight="xtermPageHight"
        ></EventXterm>
        <CMDXterm v-else-if="conn.name.startsWith('CMD-')"
          :conn="conn"
          :containerName="containerName"
          :connListLength="connListLength"
          :xtermPageHight="xtermPageHight"
          @submitUserInput="submitUserInput"
          @connectionListClick="connectionListClick"
        ></CMDXterm>
        <CodeXterm v-else-if="conn.name.startsWith('CODE-')"
          :conn="conn"
          :containerName="containerName"
          :connListLength="connListLength"
          :xtermPageHight="xtermPageHight"
          @submitUserInput="submitUserInput"
          @connectionListClick="connectionListClick"
        ></CodeXterm>
        <ConnXterm v-else
          :message="message"
          :conn="conn"
          :containerName="containerName"
          :connListLength="connListLength"
          :xtermPageHight="xtermPageHight"
          @submitUserInput="submitUserInput"
          @connectionListClick="connectionListClick"
        ></ConnXterm>
      </v-col>
    </v-row>
    <AskQuestion
      :question="question"
      :containerName="containerName"
    ></AskQuestion>
    <v-dialog
      max-width="800"
      v-model="dialog"
    >
      <v-card>
        <v-card-title></v-card-title>
        <v-card-text>
          <h1 class="black--text font-weight-light">{{ container.name }} Are You Sure to Stop Test? </h1>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn
            color="error"
            @click="containerActionClick"
          >Confirm</v-btn>
          <v-btn
            color="primary"
            @click="dialog = false"
          >Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import InfoXterm from './InfoXterm'
import ConnXterm from './ConnXterm'
import EventXterm from './EventXterm'
import CMDXterm from './CMDXterm'
import CodeXterm from './CodeXterm'
import AskQuestion from '../ContainerPage/AskQuestion'
import { hostname, KunlunApi } from '../api'
import ToolBar from '../ToolBar'
import store from '../store'

export default {
  name: 'ConnectionPage',
  components: {
    InfoXterm,
    ConnXterm,
    EventXterm,
    CMDXterm,
    CodeXterm,
    AskQuestion,
    ToolBar
  },

  data: () => ({
    xtermPageHight: 0,
    containerName: '',
    containerInfo: '',
    message: {},
    upperLevel: '',
    url: '',
    ws: null,
    dialog: false,
    //
    container: {},
    connectionList: [],
    connList: [],
    connListLength: 3,
    version: '',
    mode: 'PRODUCT',
    question: {
      name: '',
      type: 'question',
      question: '',
      options: [],
      multiple: false,
      visible: true,
      image: ''
    }
  }),
  mounted () {
    this.xtermPageHight = document.body.clientHeight - 230
    this.url = window.location.hash.substring(1)  // "/kunlun/pcbst"
    const path = window.location.hash.split('/')
    this.containerName = (path[2] + ':' + path[3]).toUpperCase()
    this.upperLevel = '/' + path[1] + '/' + path[2]
    this.initSocket()
  },
  beforeDestroy () {
    this.ws.close()
  },
  computed: {
    containerAction () {
      if (this.container.status === 'idle') { return 'Start Test' }
      else if (this.container.status === 'run') { return 'Stop Test' }
      else if (this.container.status === 'stop' || this.container.status === 'fail' || this.container.status === 'pass') { return 'Deposit Test' } 
      else { return 'Start Test' }
    }, 
    containerIcon () {
      if (this.container.status === 'idle') { return 'play_arrow' }
      else if (this.container.status === 'run') { return 'highlight_off' }
      else if (this.container.status === 'stop' || this.container.status === 'fail' || this.container.status === 'pass') { return 'delete_forever' } 
      else { return 'play_arrow' }
    },
    modeIcon () {
      if (this.container.mode === 'PRODUCT') { return 'stars' }
      if (this.container.mode === 'DEVELOP') { return 'bug_report' } 
      return ''
    },
    containerColor () {
      if (this.questionContainer === this.container.name && this.container.status === 'run') { return 'info' }
      else if (this.container.status === 'run') { return 'orange' }
      else if (this.container.status === 'idle') { return 'primary' }
      else if (this.container.status === 'fail' || this.container.status === 'stop') { return 'error' } 
      else if (this.container.status === 'pass') { return 'success' }
      else { return 'primary' }
    },
  },
  methods: {
    initSocket () {
      this.getPlatformVersion()
      const url = hostname.replace('http', 'ws') + '/ws' + this.url
      this.ws = new WebSocket(url)
      this.ws.onmessage = this.onMessageSocket
      this.ws.onerror = this.onErrorSocket
      this.ws.onclose = this.onCloseSocket
    },
    onErrorSocket () {
      store.commit('setMessage', "WS Connection Lost")
      setTimeout(() => {
        this.initSocket()
      }, 3000)
    },
    onSendSocket (data) {
      this.ws.send(JSON.stringify(data))
    },
    onCloseSocket () {
      return false
    },
    onMessageSocket (e) {
      const data = JSON.parse(e.data)
      if (data.type === 'container') {
        this.container = data.data
      }
      if (data.type === 'message') {
        // console.log(data)
        this.message = data
      }
      if (data.type === 'question') {
        this.question = data.data
        // console.log(this.question)
      }
    },
    connectionListClick (conn) {
      // console.log(conn)
      const index = this.connList.indexOf(conn)
      // console.log(this.connList)
      // console.log(index)
      if (index !== -1) {
        this.connList.splice(index, 1)
      } else {
        if (this.connListLength > 2) { return false }
        this.connList.push(conn)
      }
      this.connListLength = this.connList.length
    },
    getContainer () {
      KunlunApi({ service: 'get-a-container', container: this.containerName })
        .then(response => {
          const data = response.data.data
          if (data.container) {
            this.container = data.container.value
          }
          this.connectionList = data.connection_list
          this.connList = this.connectionList.slice(1,3)
          this.connListLength = this.connList.length
        })
        .catch(e => {
          console.log(e)
        })
    },
    containerActionClick () {
      let service = 'deposit-test'
      if (this.container.status === 'idle') { service = 'start-test'}
      if (this.container.status === 'run') { 
        if (this.dialog == false) {
          this.dialog = true
          return false
        }
        service = 'stop-test'
        this.question.container = ""
      }
      KunlunApi({ service: service, container: this.containerName, mode: this.mode, username: 'kunlun' })
        .then(response => {
          store.commit('setMessage', response.data.message)
          this.dialog = false
        })
        .catch(e => {
          console.log(e)
        })
    },
    getBackClick () {
      this.$router.push(this.upperLevel)
    },
    submitUserInput (data) {
      this.onSendSocket(data)
    },
    getPlatformVersion () {
      KunlunApi({ service: 'get-platform-version' })
        .then(response => {
          this.version = response.data.data
          this.getContainer()
        })
        .catch(e => {
          console.log(e)
        })
    },
    setMode (mode) {
      this.mode = mode
    }
  }
}
</script>
<style scoped>

</style>